<template>
<div class="fillContainer">
  <div>
    <el-form  :inline="true" ref="add_data">
      <el-form-item class="btnRight">
        <el-button type="primary" size="small" icon="view" @click="handleAdd()">添加</el-button>
      </el-form-item>
    </el-form>
  </div>
  <div class="table_container">
    <el-table
    :data="tableData"
    border 
    height="400"
    style="width: 100%;">
     <el-table-column
      align="center"
      type="index"
      label="序号"
      width="70">
    </el-table-column>
    <el-table-column
      align="center"
      label="创建时间"
      width="250">
      <template slot-scope="scope">
        <i class="el-icon-time"></i>
        <span style="margin-left: 10px">{{ scope.row.createdAt}}</span>
      </template>
    </el-table-column>
    <el-table-column 
    align="center"
    prop="type"
    label="收支类型"
    width="150">
    </el-table-column>
    <el-table-column 
    align="center"
    prop="describe"
    label="收支描述"
    width="180">
    </el-table-column>
    <el-table-column 
    align="center"
    prop="income"
    label="收入"
    width="170">
    <template slot-scope="scope">
        <span style="color:#00d053">{{ scope.row.income}}</span>
      </template>
    </el-table-column>
    <el-table-column 
    align="center"
    prop="expend"
    label="支出"
    width="170">
    <template slot-scope="scope">
        <i class="el-icon-s-promotion"></i>
        <span style="color:#f56767">{{ scope.row.expend}}</span>
      </template>
    </el-table-column>
    <el-table-column 
    align="center"
    prop="cash"
    label="账户现金"
    width="170">
    <template slot-scope="scope">
        <i class="el-icon-s-promotion"></i>
        <span style="color:#4db3ff">{{ scope.row.cash}}</span>
      </template>
    </el-table-column>
    <el-table-column 
    align="center"
    prop="remark"
    label="备注"
    width="220">
    </el-table-column>
    <el-table-column label="操作" prop="operation" align="center"  width="180">
      <template slot-scope="scope">
        <el-button
          type="warning"
          size="small"
          icon="edit"
          @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
        <el-button
          size="small"
          type="danger"
          icon="delete"
          @click="handleDelete(scope.$index, scope.row)">删除</el-button>
      </template>
    </el-table-column> 
  </el-table>
  </div>

  <eldialog :dialog="dialog" :formData="formData" @update="this.getProfile"></eldialog>
</div>
</template>

<script>
import axios from 'axios'
import Dialog from '../components/Dialog'
export default {
  name: 'fundlist',
  data() {
    return {
      tableData: [],
      formData: {
        type:'',
        describe:"",
        income:"",
        expend:"",
        cash:"",
        remark:"",
        objectId:""
      },
      dialog: {
        show: false,
        title: '',
        option:'edit'   //判断option的值确定编辑或添加
      }
    }
  },
  components: {
    'eldialog': Dialog
  },
  created() {
    this.getProfile();
  },
  methods: {
    getProfile() {
      // 获取表格数据
     axios({
       url: ' /api/1/classes/fundlist',
       method: 'get',
       headers: {
         "X-Bmob-Application-Id": "3486ee36532cacc0aad46564211c4af1",
         "X-Bmob-REST-API-Key": "75f41593b456f26a8fbca37d28032c5e",
         "Content-Type": "application/json",
       }
     }).then(res => {
       console.log(res);
       this.tableData=res.data.results;
      //  console.log(this.tableData);
     });
      
    },
    handleEdit(index, row) {
      console.log(123);
      //  编辑
      this.dialog= {
        show: true,
        title: '修改资金信息',
        option: 'edit'
      };
      this.formData= {
        type:row.type,
        describe: row.describe,
        income: row.income,
        expend: row.expend,
        cash: row.cash,
        remark: row.remark,
        objectId: row.objectId
      }
    },
    handleDelete(index, row) {
      console.log(456);
    },
    handleAdd() {
      //  添加
      // console.log(789);
      this.dialog= {
        show: true,
        title: '添加资金信息',
        option: 'add'
      };
      this.formData= {
        type:'',
        describe: '',
        income: '',
        expend: '',
        cash: '',
        remark: ''
      };
    }
  }
}
</script>


<style scoped>
.fillContainer {
  width: 100%;
  height: 100%;
  padding: 16px;
  box-sizing: border-box;
}
.btnRight{
  float: right;
}
</style>
