  <div id="app">

    <router-view/>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: "app",
  components: {},
  methods: {
    submitForm(formname) {
      this.$refs[formname].validate(valid => {
        if (valid) {
          // alert("submit");
          //测试bomb的rest api
         axios({
           method: 'GET',
           url: 'https://api2.bmob.cn/1/login',
           headers: {
             "X-Bmob-Application-Id": "3486ee36532cacc0aad46564211c4af1",
             "X-Bmob-REST-API-Key": "75f41593b456f26a8fbca37d28032c5e",
             "Content-Type": "application/json",
            //  "Access-Control-Allow-Origin ": "*"
           },
           params: {
             "username": this.loginUser.email,
             "password": this.loginUser.password
           },
         }).then(res=>{
           // 登陆成功
           
           console.log(res);
           const { sessionToken } = res.data;
           // 存储到localstorage
           localStorage.setItem('eleToken',sessionToken);
            //存储到vuex中
          this.$store.dispatch('setisAuth',!this.isEmpty(res.data));
          this.$store.dispatch('setuser',res.data);
          this.$message({
             message: "账号登陆成功！",
             type: "success"
           })
           
           this.$router.push("/index");
         })
         .catch(err=>{
           this.$message({
             message: "账号与密码不匹配",
             type: "error"
           })
         })
         ;

         
        } else {
          console.log("error submit");
        }
      });
    },
    isEmpty(value) {
      return (
        value === undefined ||
        value === null ||
        (typeof value === "object" && Object.keys(value).length === 0) ||
        (typeof value === "string" && value.trim().length===0)
      )
    }
  },
  created() {
    if(localStorage.eleToken){
      
    }
  }
};
</script>

