<template>
  <div class="register">
    <section class="form_container">
      <div class="manage_tip">
        <span class="title">在线后台管理系统</span>
        <el-form
          :model="loginUser"
          status-icon
          :rules="rules"
          ref="loginForm"
          label-width="60px"
          class="loginForm"
        >
          <el-form-item label="邮箱" prop="email">
            <el-input type="text" v-model="loginUser.email" placeholder="请输入email"></el-input>
          </el-form-item>
          <el-form-item label="密码" prop="password">
            <el-input type="password" v-model="loginUser.password" placeholder="请输入密码"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" class="submit_btn" @click="submitForm('loginForm')">登陆</el-button>
          </el-form-item>
          <div class="tiparea">
            <p>还没有账号？现在<router-link to="/register">注册</router-link></p>
          </div>
        </el-form>
      </div>
    </section>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: "register",
  components: {},
  data() {
    return {
      loginUser: {
        email: "",
        password: "",
      },
      rules: {
        email: [
          {
            type: "email",
            required: true,
            message: "邮箱格式不正确",
            trigger: "blur"
          }
        ],
        password: [
          {
            required: true,
            message: "密码不能为空",
            trigger: "blur"
          },
          {
            min: 6,
            max: 30,
            message: "长度在6到30之间",
            trigger: "blur"
          }
        ]
      }
    };
  },
  methods: {
    submitForm(formname) {
      this.$refs[formname].validate(valid => {
        if (valid) {
          // alert("submit");
          //测试bomb的rest api
         axios({
           method: 'GET',
           url: '/api/1/login',
           headers: {
             "X-Bmob-Application-Id": "3486ee36532cacc0aad46564211c4af1",
             "X-Bmob-REST-API-Key": "75f41593b456f26a8fbca37d28032c5e",
             "Content-Type": "application/json",
            //  "Access-Control-Allow-Origin ": "*"
           },
           params: {
             "username": this.loginUser.email,
             "password": this.loginUser.password
           },
         }).then(res=>{
           // 登陆成功
           
          //  console.log(res);
           //  存储到sessionStorage
          //  sessionStorage.setItem('login',JSON.stringify())
           const { sessionToken } = res.data;
           // 存储到localstorage
           localStorage.setItem('eleToken',sessionToken);
            //存储到vuex中
          this.$store.dispatch('setisAuth',!this.isEmpty(res.data));
          this.$store.dispatch('setuser',res.data);
          this.$message({
             message: "账号登陆成功！",
             type: "success"
           })
           
           this.$router.push("/index");
         })
         .catch(err=>{
           this.$message({
             message: "账号与密码不匹配",
             type: "error"
           })
         })
         ;

         
        } else {
          console.log("error submit");
        }
      });
    },
    isEmpty(value) {
      return (
        value === undefined ||
        value === null ||
        (typeof value === "object" && Object.keys(value).length === 0) ||
        (typeof value === "string" && value.trim().length===0)
      )
    }
  },
  created() {
    
  }
};
</script>

<style scoped>
.register {
  position: relative;
  width: 100%;
  height: 100%;
  background: url("../../public/img/bg.jpg");
  background-size: 100% 100%;
}
.form_container {
  width: 370px;
  height: 210px;
  position: absolute;
  top: 10%;
  left: 34%;
  padding: 25px;
  border-radius: 5px;
  text-align: center;
}

.form_container .manage_tip .title {
  font-family: "Microsoft YaHei";
  font-weight: bold;
  font-size: 26px;
  color: #fff;
}
.loginForm {
  margin-top: 20px;
  background-color: #fff;
  padding: 20px 40px 20px 20px;
  border-radius: 5px;
  box-shadow: 0 5px 10px #cccc;
}
.submit_btn {
  width: 100%;
}
.tiparea {
  text-align: right;
  font-size: 12px;
  color: #333;
}
.tiparea p a {
  color: #409eff;
}
</style>
