import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const state = {
  isAuth: false,
  user: {}
}

const getters = {
  isAuth: state => state.isAuth,
  user: state => state.user
}

const mutations = {
  changeisauth (state, isAuth) {
    if (isAuth) {
      state.isAuth = isAuth
    } else {
      state.isAuth = false
    }
  },
  changeuser (state, user) {
    if (user) {
      state.user = user
    } else {
      state.user = {}
    }
  }
}

const actions = {
  setisAuth: ({ commit }, isauth) => {
    commit('changeisauth', isauth)
  },
  setuser: ({ commit }, user) => {
    commit('changeuser', user)
  },
  clearCurrentState: ({ commit }) => {
    commit('changeisauth', false)
    commit('changeuser', null)
  }
}

export default new Vuex.Store({
  state,
  getters,
  mutations,
  actions
})
